.site-header,
.site-footer{
  height: 50px;
}
.site-header .nav{
  height: 100%;
}
.site-header a {
	color: inherit;
}
.site-header .nav-tabs{
  border : none;
  background: #606c88;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3f4c6b, #606c88); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
}
.site-header .nav-tabs .nav-item{
	margin-bottom: 0
}
.site-header .nav .nav-link{
  display: flex;
  align-items: center;
  height: 100%;
  border: none;
  border-radius : 0.25rem;
}
.site-header .nav-tabs .nav-link:hover,
.site-header .nav-tabs .nav-link.active,
.site-header .nav-tabs .nav-item.show .nav-link{
	background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	color: #fff;
}
body {
  background: #eaebef;
  color: #212121;
  font-family: "Courier New", Courier, monospace;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body.simple-login-app {
    background-color: transparent; }
  body button {
    cursor: pointer; }
  body .route-holder {
    min-height: calc(100vh - 100px); }
  body .site-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #606c88;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #3f4c6b, #606c88);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: #fff; }
  body .app-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: #7F7FD5;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }
  body .under-construction,
  body .not-found {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: calc(100vh - 150px);
    color: #fff;
    font-size: 4rem; }
  body .App {
    text-align: center; }
  body .nav-bar {
    display: flex;
    justify-content: flex-end;
    padding: 0 10px; }
    body .nav-bar .nav-item {
      margin-left: 20px; }
      body .nav-bar .nav-item:hover {
        text-decoration: underline; }
  body .center {
    display: flex;
    justify-content: center; }
  body .white-card {
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.32);
    padding: 15px;
    margin-top: 20px;
    position: relative;
    min-width: 400px; }
    body .white-card:first-child {
      margin-top: 0; }
  body .cursor-pointer {
    cursor: pointer; }
  body .field-wrapper .field-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px; }
    body .field-wrapper .field-container:last-child {
      margin-bottom: 0; }
  body .submit-btn-wrapper {
    margin-top: 10px; }
    body .submit-btn-wrapper .submit-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 200px;
      background: #5d6c74;
      color: #fff;
      border-radius: 20px;
      height: 40px;
      cursor: pointer;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
      margin: auto;
      font-size: 16px;
      border: none;
      outline: 0; }
      body .submit-btn-wrapper .submit-btn:hover {
        background: #425b68; }
  body .user-profile-wrapper {
    background-size: contain;
    border-radius: 50%;
    width: 44px;
    margin: 0 8px 0 0;
    padding: 2px; }
    body .user-profile-wrapper .user-profile-icon {
      border: 2px solid #fff;
      width: 40px;
      height: 40px;
      border-radius: 50px;
      background-size: 100% 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

